#project_container_div {
  /* height: 200px;
    width: 200px; */
  background-color: rgb(255, 255, 255);
  margin: 20px;
  position: relative;
  display: flex;
  border-radius: 15px;
  height: 300px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#project_div {
  overflow: scroll;
  overflow-x: auto;
  margin-top: 20px;
  height: 55vh;
}

.project_image {
  border-radius: 15px;
  max-height: 200px;
  object-fit: cover;
  width: 200px;
  height: 200px;
}

.project_name {
  /* position: absolute; */
  /* bottom: 10px; */
  display: flex;
  width: 100%;
  justify-content: center;
  font-size: 30px;
}

.project_name_div {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  /* background-color: white; */
}

.project_description_p {
  margin: 20px;
  margin-left: 0px;
  max-width: 500px;
}

h3 {
  margin-bottom: 0px;
}

.zoom_p {
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.zoom_p::after {
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.zoom_p:hover {
  -webkit-transform: scale(1.1, 1.1);
  transform: scale(1.1, 1.1);
}

.zoom_p:hover::after {
  opacity: 1;
}

.tooltip_p {
  position: relative;
  display: inline-block;
  /* width: 100%; */
  /* If you want dots under the hoverable text */
}

/* Tooltip text */

.tooltip_p .tooltiptext_p {
  visibility: hidden;
  /* width: 75px; */
  background-color: black;
  opacity: 0.5;
  color: #fff;
  text-align: center;
  /* padding: 10px; */
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  bottom: 15%;
  left: 5%;
  min-width: 70%;
  margin: 0px 10px;
  /* margin-left: -60px; */
}

/* Show the tooltip text when you mouse over the tooltip container */

.tooltip_p:hover .tooltiptext_p {
  visibility: visible;
  font-size: 16px;
}

.tooltip_n {
  position: relative;
  display: inline-block;
  /* width: 100%; */
  /* If you want dots under the hoverable text */
}

/* Tooltip text */

.tooltip_n .tooltiptext_n {
  visibility: hidden;
  /* width: 75px; */
  background-color: black;
  opacity: 0.5;
  color: #fff;
  text-align: center;
  /* padding: 10px; */
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  bottom: 10%;
  left: 25%;
  width: 40%;
  /* margin: 0px 10px; */
  margin: 0px auto;
  /* margin-left: -60px; */
}

/* Show the tooltip text when you mouse over the tooltip container */

#project_img_div {
  display: flex;
  align-items: center;
  margin: auto 50px;
}

.tooltip_n:hover .tooltiptext_n {
  visibility: visible;
  font-size: 16px;
}

#words {
  color: white;
}

@media screen and (min-width: 930px) {}

@media screen and (max-width: 930px) {
  #project_container_div {
    flex-direction: column;
    height: auto;
    /* margin-top: 15px; */
    /* height: ; */
  }
  .project_description_p {
    margin: 20px;
    max-width: 500px;
  }
  #project_img_div {
    width: 100%;
    margin: 0px auto;
    display: flex;
    justify-content: center;
  }
  .project_image {
    margin: 20px;
  }
  .project_name {
    margin-top: 0px;
    font-size: 25px;
    text-align: center;
  }
  h1 {
    margin-left: 15px;
  }
  #project_div {
    height: 69vh;
  }
}

@media screen and (max-width: 481px) {
  .project_image {
    height: 150px;
  }
}

@media screen and (max-width: 330px) {
  #project_div {
    margin-top: 0px;
  }
  #project_container_div {
    margin: 5px;
    /* box-shadow: none; */
  }
}